import articleApi from '@/model/article';
export default {
  name: 'OrderPayDialog',
  data() {
    return {
      isPaid: false,
      isTrans: false,
      payUrl: '',
      dialogTableVisible: false,
      congratulationsVisible: false,
      // 控制恭喜信息对话框的显示
      form: {
        amount: 0,
        create_time: 'AAA',
        payment_id: 'AAA',
        payment_no: 'AAA',
        status: 'AAA',
        user_id: 0
      },
      activeIndex: '',
      loading: false,
      info: '',
      // 添加倒计时相关数据
      remainingTime: 300,
      // 5分钟倒计时，单位秒
      maxTime: 300,
      // 最大时间
      countdownTimer: null // 倒计时定时器
    };
  },

  computed: {
    // 计算倒计时进度百分比
    timePercentage() {
      return this.remainingTime / this.maxTime * 100;
    }
  },
  watch: {
    dialogTableVisible(val) {
      if (!val) {
        this.loading = false;
        this.isPaid = false;
        this.isTrans = false;
        // 清除倒计时
        if (this.countdownTimer) {
          clearInterval(this.countdownTimer);
          this.countdownTimer = null;
        }
      }
    }
  },
  methods: {
    show(key, title) {
      this.form = key;
      this.info = title || 'VIP · 1年';
      this.dialogTableVisible = true;
      this.activeIndex = this.form.payment_no;
      this.loading = false;
      this.startCheckPay(this.form.payment_id); // 自动启动支付状态检查
      this.startCountdown(); // 启动倒计时
    },

    closeCongratulations() {
      this.congratulationsVisible = false;
      this.loading = false; // 重置loading状态
    },

    async CheckPay(payment_id) {
      try {
        // 尝试获取当前用户信息
        const res = await articleApi.postOrderNoPayCheck2(payment_id);
        if (res === "支付成功") {
          this.form.status = res;
          // 关闭定时调用checkPay
          this.dialogTableVisible = false;
          this.congratulationsVisible = true;
          clearInterval(this.checkPayInterval);
          // 清除倒计时
          if (this.countdownTimer) {
            clearInterval(this.countdownTimer);
            this.countdownTimer = null;
          }
          this.$message.success('恭喜您成为邻诗会员！');
          //查询订单内容
        }
      } catch (e) {
        console.log(e);
      }
    },
    startCheckPay(payment_id) {
      // 设置定时器，每 5 秒调用一次 CheckPay
      const maxTime = 300 * 1000; // 5 分钟，单位是毫秒
      let elapsedTime = 0; // 已经过去的时间，单位是毫秒

      // 每隔 5 秒执行一次 CheckPay
      this.checkPayInterval = setInterval(async () => {
        // 调用 CheckPay 方法检查支付状态
        await this.CheckPay(payment_id);

        // 累加已过去的时间
        elapsedTime += 5000;

        // 如果超过 5 分钟，停止定时器
        if (elapsedTime >= maxTime) {
          clearInterval(this.checkPayInterval);
          console.log('Payment check timeout.');
          //并且关闭对话框
          this.form.status = '支付失败';
          // 清除倒计时
          if (this.countdownTimer) {
            clearInterval(this.countdownTimer);
            this.countdownTimer = null;
          }
        }
      }, 5000); // 5秒调用一次 CheckPay
    },

    // 格式化时间为 mm:ss 格式
    formatTime(seconds) {
      const minutes = Math.floor(seconds / 60);
      const remainingSeconds = seconds % 60;
      return `${minutes.toString().padStart(2, '0')}:${remainingSeconds.toString().padStart(2, '0')}`;
    },
    // 启动倒计时
    startCountdown() {
      // 重置倒计时
      this.remainingTime = this.maxTime;

      // 清除可能存在的旧定时器
      if (this.countdownTimer) {
        clearInterval(this.countdownTimer);
      }

      // 创建新的倒计时定时器
      this.countdownTimer = setInterval(() => {
        if (this.remainingTime > 0) {
          this.remainingTime -= 1;
        } else {
          // 倒计时结束，清除定时器
          clearInterval(this.countdownTimer);
          this.countdownTimer = null;
        }
      }, 1000);
    }
  },
  // 组件销毁时清除所有定时器
  beforeUnmount() {
    if (this.checkPayInterval) {
      clearInterval(this.checkPayInterval);
    }
    if (this.countdownTimer) {
      clearInterval(this.countdownTimer);
    }
  }
};