import OrderPayDialog from '@/view/order/order-pay-dialog';
import articleApi from '@/model/article';
export default {
  components: {
    OrderPayDialog
  },
  data() {
    return {
      vipTypes: {
        0: {
          name: '20次卡',
          price: 9.9
        },
        1: {
          name: '月卡',
          price: 19.9
        },
        2: {
          name: '年卡',
          price: 49.9
        },
        3: {
          name: '首充卡',
          price: 4.9
        },
        4: {
          name: '主播卡',
          price: 29.9
        }
      }
    };
  },
  methods: {
    async showPay(type) {
      if (this.loggedIn) {
        let order = await articleApi.postOrderNo2(type);
        if (order.payment_no != '') {
          const vipTitle = this.vipTypes[type] ? this.vipTypes[type].name : '未知会员卡';
          this.$refs['orderPay'].show(order, vipTitle);
        } else {
          this.$message.error('获取订单号失败');
        }
      } else {
        this.$message.error('请先登录后再下单');
      }
    }
  },
  computed: {
    loggedIn() {
      return this.$store.state.loggedIn;
    }
  }
};